"use client";

import Image from "next/image";
import { useEffect, useState } from "react";
import { ArrowUp } from "lucide-react";

export default function Navbar() {
  const [showButton, setShowButton] = useState(false);

  // Show scroll-to-top button
  useEffect(() => {
    const handleScroll = () => setShowButton(window.scrollY > 300);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const scrollToTop = () => window.scrollTo({ top: 0, behavior: "smooth" });

  // Smooth scroll to section
  const scrollToSection = (id: string) => {
    const element = document.getElementById(id);
    if (element) {
      const offset = -70; // navbar height
      const top = element.getBoundingClientRect().top + window.scrollY + offset;

      window.scrollTo({ top, behavior: "smooth" });
    }
  };

  return (
    <>
      {/* Navbar */}
      <header className="fixed top-0 left-0 right-0 z-50 bg-white/90 backdrop-blur-md border-b border-gray-200 shadow-sm">
        <div className="mx-auto max-w-7xl px-8 py-3 flex items-center justify-between">

          {/* Logo */}
          <div
            onClick={scrollToTop}
            className="flex items-center gap-2 cursor-pointer hover:scale-[1.03] transition"
          >
            <Image
              src="/images/bracefree.png"   // CHANGE DONE HERE ONLY
              alt="Smileverse"
              width={120}
              height={12}
              priority
              className="object-contain"
            />
          </div>

          {/* Right Menu */}
          <nav className="flex items-center gap-8 text-gray-800 font-medium">

            <button
              onClick={scrollToTop}
              className="hover:text-blue-500 transition"
            >
              Home
            </button>

            <button
              onClick={() => scrollToSection("about")}
              className="hover:text-blue-500 transition"
            >
              About
            </button>

            <button
              onClick={() => scrollToSection("vision")}
              className="hover:text-blue-500 transition"
            >
              Our Vision
            </button>

            <button
              onClick={() => scrollToSection("faq")}
              className="hover:text-blue-500 transition"
            >
              FAQs
            </button>

          </nav>
        </div>
      </header>

      {/* Spacer to prevent content jump */}
      <div className="h-[65px]" />

      {/* Scroll-to-Top */}
      {showButton && (
        <button
          onClick={scrollToTop}
          aria-label="Scroll to top"
          className={`
            fixed bottom-6 right-6 z-50 flex items-center justify-center
            w-[50px] h-[50px] rounded-full backdrop-blur-md bg-white/90 
            border border-gray-300 shadow-md text-gray-800 
            hover:bg-gray-100 hover:shadow-lg transition-all duration-300
            ${showButton ? "opacity-100" : "opacity-0 translate-y-4 pointer-events-none"}
          `}
        >
          <ArrowUp size={22} strokeWidth={2.5} />
        </button>
      )}
    </>
  );
}
